<script>
import { ref } from "vue";
import DetailComponent from "@/components/DetailComponent.vue";
import SelectComponent from "@/components/SelectComponent.vue";
import { useMenuStore } from "@/stories/mainStore.js";

export default {
  name: "AlamView",
  components: {
    DetailComponent,
    SelectComponent,
  },
  setup() {
    //
    const menuStore = useMenuStore();
    const tabIdx = ref(0);
    // const tabs = ref([
    //   "Network Status",
    //   "Communication",
    //   "Interface Status",
    //   "Command History",
    //   "Log",
    // ]);

    const tabsMap = {
      command: "Command",
      command_history: "Command History",
      communication: "Communication",
      interface_status: "Interface Status",
      network_status: "Network Status",
      sw_log: "SW Log",
    };

    const tabArr = [];
    menuStore.tabs.get(location.pathname.replace(/\//, "")).map((item) => {
      if (!tabsMap[item.name]) {
        console.warn(`누락 ${item.name}`);
      } else {
        tabArr.push(tabsMap[item.name]);
      }
    });
    const tabs = ref(tabArr);

    return {
      tabs,
      tabIdx,
    };
  },
};
</script>

